@import url('https://fonts.googleapis.com/css2?family=Hammersmith+One&display=swap');

.menu-main{
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    width: 704px;
    /* transform: translate(86px, -75px); */
    transform: translate(146px, -74px);
    position: absolute;
    align-items: flex-end;

}
.home-new-pos{
 
  transform: translate(6px, 186px);
  animation: revealRightHome 1.5s forwards;
  animation-delay: 4s;
  clip-path: inset(0% 0% 100% 100% );
}
@keyframes revealRightHome {
  0% {
    clip-path: inset(0% 0% 95% 95% );
  }
  50% {
    clip-path: inset(0% 0% 0% 95%);
  }
  75% {
    clip-path: inset(0% 0% 0% 95%);
  }
  100%{
    clip-path: inset(0% 0% 0% 0%);
  }
}
.menu-main li{
    background-color: #CD5A33;
    /* border: 2px solid black; */
    /* border-radius: 20px; */
    width: fit-content;
    /* font-weight: bolder; */
    text-transform: uppercase;
    padding: 40px;
    font-family: 'Hammersmith One', sans-serif;
    font-size: 42px;
    max-width: 320px;
    text-align: center;
    align-items: center;
    margin-right: 0.5em;
    margin-top: 0.5em;
    display: flex;
    cursor: pointer;
    position: relative;
    color: #FFD9BC;
    overflow: hidden;
  box-shadow: 10px 10px 24px 0 rgba(0,0,0,0.45);
}

.menu-main li::before{
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 230%;
    height: 100%;
    opacity: 0.2;
    background-image: url("../assets/texture-1.jpg");
    background-repeat: no-repeat;
    background-position: 50% 0;
    background-size: cover;
  animation: texture 8s linear infinite;
  animation-direction:alternate ;

  }

  @keyframes texture {
    0% {background-position: -300px 0;}
    100% {background-position: 0 0;}

  }
